export default {
    /**
    * @function 
    * @parampara,
    * @returns: String 
    * */
    tryType: para => {
        const type = typeof para;
        if (type === "number" && isNaN(para)) return "NaN";
        if (type !== "object") return type;
        return Object.prototype.toString
            .call(para)
            .replace(/[\[\]]/g, "")
            .split(" ")[1]
            .toLowerCase();
    },
    /**
    * @function: 
    * @param num
    * @returns: Boolen
    * */
    isNumber: para => {
        if (window.isNumber) return window.isNumber(para);
        if (Number.isNumber) return Number.isNumber(para);
        return typeof para !== "undefined" && !isNaN(para);
    },
    /**
    * @function: props20,20px,20rem,20em
    * @param num
    * @returns: Boolen
    * */
    testUnitStr: (para) => {
        return /^[0-9]+(px|em|rem|)$/.test(para);
    },

    /**
     * @function: ID
     * @returns: String
     */
    gId: () => {
        function r() {
            return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        }
        return (`${r() + r()}-${r()}-${r()}-${r()}-${r() + r() + r()}`);
    }
};



